body {
  background-image: linear-gradient(
  180deg,
  hsl(0deg 0% 100%) 0%,
  hsl(112deg 40% 84%) 15%,
  hsl(97deg 46% 70%) 31%,
  hsl(80deg 53% 57%) 49%,
  hsl(95deg 42% 51%) 73%,
  hsl(116deg 39% 46%) 93%,
  hsl(139deg 64% 34%) 100%
  );
}

.modal-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 550px;
  background: url(./images/portalDifuminado.png) no-repeat center center;
  background-size: 500px 500px;
  border: 0;
}

.modal-footer {
  border: 0;
}

.card-name:hover {
  background-color: #5BBFC0;
  cursor: pointer;
  transform: scale(1.05);
  transition: transform 0.2s;
}

.containerButtonUp {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.imgButtonUp {
  width: 12%;
}

.containerTitle {
  display: flex;
  justify-content: center;
  text-align: center;
  background-color: #5BBFC0; /* En el cuarto parametro metemos la opacidad -> opacity: .5; De esta forma no le afecta la opacidad al texto dentro del div contenedor que queremos que sea medio transparente*/
}

.gif {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.loader-container {
  height: 100px;
  width: 100px;
}

.navbar-img {
  height: 60px;
  width: 60px;
  padding: 10px;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar {
  /* background-color: #75cbed; */
  background-image: linear-gradient(
    270deg,
    hsl(111deg 42% 75%) 0%,
    hsl(100deg 45% 71%) 14%,
    hsl(92deg 47% 67%) 24%,
    hsl(85deg 50% 62%) 30%,
    hsl(80deg 53% 57%) 35%,
    hsl(84deg 48% 55%) 39%,
    hsl(90deg 44% 53%) 43%,
    hsl(98deg 41% 50%) 48%,
    hsl(108deg 40% 48%) 54%,
    hsl(114deg 39% 46%) 61%,
    hsl(121deg 39% 44%) 70%,
    hsl(129deg 47% 40%) 83%,
    hsl(139deg 64% 34%) 100%
  );
}

.character-img:hover {
  cursor: pointer;
  transform: scale(1.06);
  transition: transform 0.2s;
}

.imgNotFound {
  margin: 15px 0 15px 0;
}

.navbar-title {
  font-family: "get_schwifty";
  font-size: x-large;
}

.letters {
  font-family: "get_schwifty";
}

.roundedCard {
  border-radius: 20px;
}

@font-face {
  font-family: "get_schwifty";
  src: url("./fonts/get_schwifty.ttf");
}
